body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
  background-color: gray;
}

.App {
  text-align: center;
}

.game-info {
  display:flex;
}

.board {
  padding: 20px;
  border-top : solid 20px darkgray;
  border-right: solid 20px rgb(143, 143, 143);
  border-bottom: solid 20px rgb(143, 143, 143);
  border-left: solid 20px darkgray;
}

.square {
  position: relative;
  background-color: darkgreen;
  border : solid 1px black;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
}

.square.placeable {
  background-color: green;
}

.board-row {
  display: flex;
}

.Stone {
  border-radius: 50%;
  height: 40px;
  width: 40px;
}

.Stone.black {
  background-color: black;
}

.Stone.white {
  background-color: cornsilk;
}

.noStone {
  height: 49px;
  width: 49px;
  opacity: 0;
}

.noStone:hover {
  opacity: 0.2;
}


